import React, {Component} from 'react';

export default class WeatherList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            weatherList: []
        }
    }

    componentDidMount() { //Получаем API погоды указанного города за 6 дней
        fetch("http://api.openweathermap.org/data/2.5/forecast/?q=" + this.props.currentCity + "&APPID=754787b8de137a5841c87db18074d452&cnt=6&units=metric")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        weatherList: result.list
                    });
                }
            )
    }

    render() {
        const {weatherList} = this.state;
        return (
            <div className='WeatherList'>
                <div className="row justify-content-center">
                    {weatherList.map((item) => {
                        const date = item.dt_txt;
                        return (
                            <div className="col-sm-2" key={item.dt}>
                                {item.dt_txt.substring(11, 16)}
                                <br/>
                                <img src={"http://openweathermap.org/img/wn/" + item.weather[0].icon + "@2x.png"}
                                     alt=""/>
                                <p className='List-Main'>{item.weather[0].main}</p>
                                <p className='List-Celsius'>{Math.floor(item.main.temp)}°</p>
                                <p className="List-Humidity"><i className="fas fa-tint"></i> {item.main.humidity}%</p>
                            </div>
                        )
                    })}
                </div>
            </div>
        )
    }
};
