import React,{Component} from 'react';

export default class CurrentWeather extends Component {
    constructor(props) {
        super(props);
        this.state = {
            weather: [],
            main: []
        }
    }

    componentDidMount() {
        //Получаем API погоды указанного города за 6 дней
        fetch("http://api.openweathermap.org/data/2.5/weather?q=" + this.props.currentCity + "&APPID=754787b8de137a5841c87db18074d452&units=metric")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        weather: result.weather[0],
                        main: result.main
                    });
                }
            )
    }
    render() {
        const {weather, main} = this.state;
        return (
            <div className='CurrentWeather'>
                <h3>{weather.main}</h3>
               {/* {JSON.stringify(this.state.currentWeather.weather[0].main)}
                {JSON.stringify(this.state.currentWeather.weather.main)}
                {this.state.currentWeather.weather.main}*/}
                <h1 className='WeatherCelsius'>{Math.floor(main.temp)}<sup>℃</sup></h1>
            </div>
        );
    }
}