import React,{Component} from 'react';

export default class CurrentLocation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            sys: [],
            date: new Date()
        }
    }
    tick() {
        this.setState({
            date: new Date()
        });
    }
    componentDidMount() {
        //Счётчик для таймера, живое время
        this.timerID = setInterval(
            () => this.tick(),
            1000
        );
        fetch("http://api.openweathermap.org/data/2.5/weather?q=" + this.props.currentCity + "&APPID=754787b8de137a5841c87db18074d452&units=metric")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        sys: result.sys
                    });
                }
            )
    }
    render() {
        const {sys} = this.state;
        return (
            <div className='CurrentLocation'>
                <h2><i className="fas fa-map-marker-alt"></i> {this.props.currentCity} <img src={"https://www.countryflags.io/"+sys.country+"/flat/32.png"} alt=""/></h2>
                <p>{this.state.date.toLocaleString()}</p>
            </div>
        )
    }
}